import React from "react";
import reactDOM from "react-dom";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import "./App.css";
import "../src/css/estilos_barra.css";
import "../src/css/estilos_home.css";
import "../src/css/estilos_slider.css";
import { LoginButton } from "./Login";
import { Profile } from "./Profile";
import Contacto from "./components/form";
import Clases from "./components/Clases";
import Cursos from "./components/Cursos";
import UserDashboard from "./components/user";
import NewsletterPage from "./components/Newsletterpage";
import reactRedirection from "./components/Reactredirection";
import Whatsapp from "./components/whatsapp";
import Home from "./components/home";
import Login from "./components/login";
import { Auth0Provider } from "@auth0/auth0-react";
import { useAuth0 } from "@auth0/auth0-react";
import logo from "./components/img/logo.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

function Routes() {
  const { isAuthenticated, user } = useAuth0();

  return (
    <BrowserRouter>
      <div>
        <header className="bar" id="bar">
          <Link to="/" className="logo-link">
            <img src={logo} alt="" className="img-bar" />
          </Link>
          <div className="naver">
            <Link to="/clases">
              <p className="p-bar">Clases</p>
            </Link>
            <Link to="/cursos">
              <p className="p-bar">Cursos</p>
            </Link>
            <Link to="/newslettersub">
              <p className="p-bar">Newsletter</p>
            </Link>
            <Link to="/contacto">
              <p className="p-bar">Contacto</p>
            </Link>
          </div>
          {isAuthenticated ? (
            <>
              <Profile />
              <Link to="/userdashboard">
                <img src={user.picture} className="UserIcon" />
              </Link>
            </>
          ) : (
            <LoginButton />
          )}
        </header>
        <Whatsapp />
        <Switch>
          <Route path="/" component={Home} exact={true} />
          <Route path="/react" component={reactRedirection} />
          <Route path="/clases" component={Clases} />
          <Route path="/cursos" component={Cursos} />
          <Route path="/newslettersub" component={NewsletterPage} />
          <Route path="/contacto" component={Contacto} />
          <Route path="/userdashboard" component={UserDashboard} />
          <Route path="/login" component={Login} />
        </Switch>
      </div>
    </BrowserRouter>
  );
}

let ubicacionPrincipal = window.pageYOffset;
window.onscroll = function () {
  let Desplazamiento_Actual = window.pageYOffset;
  if (ubicacionPrincipal >= Desplazamiento_Actual) {
    document.getElementById("bar").style.top = "0";
  } else {
    document.getElementById("bar").style.top = "-100px";
  }
  ubicacionPrincipal = Desplazamiento_Actual;
};

reactDOM.render(
  <React.StrictMode>
    <Auth0Provider
      domain="dev-zzyukyep.us.auth0.com"
      clientId="nn4uFxKFy7hHF0GobgBh8AYyNSNAHLjW"
      //==Localhost==//clientId="iBD8ljPTM8mzkAcAN9C1zROU26oeTHma"
      redirectUri={window.location.origin}
    >
      <Routes />
    </Auth0Provider>
  </React.StrictMode>,
  document.getElementById("root")
);
