import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import { LogoutButton } from "../Logout";
import { Link } from "react-router-dom";

class Menu extends React.Component {
  state = {
    abierto: false,
  };

  abrirModal = () => {
    this.setState({ abierto: !this.state.abierto });
    const bodyScrollLock = require("body-scroll-lock");
    const disableBodyScroll = bodyScrollLock.disableBodyScroll;
    const targetElement = document.querySelector("body");
    disableBodyScroll(targetElement);
  };
  cerrarModal = () => {
    this.setState({ abierto: !this.state.abierto });
    const bodyScrollLock = require("body-scroll-lock");
    const enableBodyScroll = bodyScrollLock.enableBodyScroll;
    const targetElement = document.querySelector("body");
    enableBodyScroll(targetElement);
  };

  render() {
    const modalStyles = {
      position: "absolute",
      top: "-35px",
      right: "-10px",
      width: "30%",
    };
    return (
      <>
        <div className="principal">
          <div className="secundario">
            <button
              color="success"
              className="btn-modal"
              id="btn-modal"
              onClick={this.abrirModal}
            ></button>
          </div>
        </div>

        <Modal isOpen={this.state.abierto} style={modalStyles}>
          <ModalHeader>
            <p>Menu</p>
          </ModalHeader>
          <ModalBody>
            <Link to="/sesiones" onClick={this.cerrarModal}>
              <button className="hover versesines">
                Ver tus sesiones tomadas
              </button>
            </Link>
            <Link to="/premium" onClick={this.cerrarModal}>
              <button className="hover logout2">Menu premium</button>
            </Link>
            <Link to="/sugerencias" onClick={this.cerrarModal}>
              <button className="hover logout3">Sugerencias</button>
            </Link>
            <Link to="/ayuda" onClick={this.cerrarModal}>
              <button className="hover logout4">Ayuda</button>
            </Link>
            <Link to="/configuracion" onClick={this.cerrarModal}>
              <button className="hover logout5">Configuraci√≥n</button>
            </Link>
            <Link to="/dudas" onClick={this.cerrarModal}>
              <button className="hover logout6">Dudas</button>
            </Link>
            <Link to="/informacion" onClick={this.cerrarModal}>
              <button className="hover logout7">Informaci√≥n</button>
            </Link>
            <Link to="/dudas" onClick={this.cerrarModal}>
              <button className="hover logout8">Otro</button>
            </Link>
            <LogoutButton />
          </ModalBody>

          <ModalFooter>
            <Button color="secondary" onClick={this.cerrarModal}>
              Cerrar
            </Button>
          </ModalFooter>
        </Modal>
      </>
    );
  }
}

export default Menu;
